@charset "UTF-8";

@font-face {
  font-family: KoPubDotum;
  font-weight: 300;
  src: url("../font/KoPubWorld Dotum Light.ttf") format("truetype");
}

@font-face {
  font-family: KoPubDotum;
  font-weight: 500;
  src: url("../font/KoPubWorld Dotum Medium.ttf") format("truetype");
}

@font-face {
  font-family: KoPubDotum;
  font-weight: 700;
  src: url("../font/KoPubWorld Dotum Bold.ttf") format("truetype");
}

@font-face {
  font-family: KoPubBatang;
  font-weight: 300;
  src: url("../font/KoPubWorld Batang Light.ttf") format("truetype");
}

@font-face {
  font-family: KoPubBatang;
  font-weight: 500;
  src: url("../font/KoPubWorld Batang Medium.ttf") format("truetype");
}

@font-face {
  font-family: KoPubBatang;
  font-weight: 700;
  src: url("../font/KoPubWorld Batang Bold.ttf") format("truetype");
}

