/* reset */
* {
  -webkit-text-size-adjust: none;
  box-sizing: border-box;
  &::before, &::after {
    box-sizing: border-box;
  }
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}
ul,
ol,
li {
  list-style: none;
}
fieldset,
img {
  border: 0;
}
br {
  letter-spacing: normal;
}
p {
  letter-spacing: normal;
}
hr {
  display: none;
}
address,
caption,
em,
var {
  padding: 0;
  margin: 0;
  font-style: normal;
  font-weight: normal;
}
a {
  text-decoration: none;
  color: #000;
}
a[href^="tel"] {
  font-style: normal;
}
a:hover,
a:active,
a:focus {
  text-decoration: none;
}
input,
textarea,
select {
  letter-spacing: normal;
  font-size: 16px;
  font-family: "KoPubDotum", sans-serif;
  &::placeholder {
    font-size: 16px;
    font-weight: 300;
    font-size: 16px;
    font-family: "KoPubDotum", sans-serif;
    color: #B6B6B6;
  }
  &:focus-visible {
    outline: 2px solid #000;
  }
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  -moz-appearance: textfield;
}
textarea {
  width: 100%;
  border-radius: 12px;
  border: 1px solid #ddd;
  padding: 10px 15px;
}
input[type="text"], input[type="number"] {
  &:disabled, &:read-only {
    background: #F8F8F8;
  }
  &.full {
    width: 100%;
  }
  height: 44px;
  padding-left: 10px;
  border-radius: 8px;
  border: 1px solid #DDDDDD;
}
input[type="date"] {
  &:disabled, &:read-only {
    background: #F8F8F8;
  }
  &::-webkit-inner-spin-button, &::-webkit-calendar-picker-indicator {
    position: relative;
    left: -11px;
    opacity: 0;
    -webkit-appearance: none;
  }
  width: 200px;
  height: 44px;
  padding-left: 10px;
  border-radius: 8px;
  border: 1px solid #DDDDDD;
  background: url(../images/ic_calendar.png) no-repeat right 12px center;
  font-size: 16px;
  font-family: "KoPubDotum", sans-serif;
}

img {
  border: 0;
  vertical-align: top;
}
legend,
caption {
  position: absolute;
  left: -1000%;
  top: 0;
  width: 1px;
  height: 1px;
  font-size: 0;
  line-height: 0;
  overflow: hidden;
}
table {
  width: 100%;
  border: 0;
  border-spacing: 0;
}
button {
  background: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
  &:focus {
    outline: #000;
  }
  /* 키보드로 버튼에 포커스 시 */
  &:focus-visible {
    outline: 2px solid #000;
  }

  /* 마우스, 터치로 버튼에 포커스 시 
  &:focus:not(:focus-visible) {
    outline: none;
    box-shadow: 1px 1px 5px rgba(1, 1, 0, .7);
  }
  */
}
.blind,
legend {
  display: block;
  overflow: hidden;
  position: absolute;
  top: -1000em;
  left: 0;
}