@charset "UTF-8";

@use "reset";


$html-font-size: 16px;

@function stripUnit($value) {
  @return $value / ($value * 0 + 1);
}
@function rem($pxValue) {
  @return #{stripUnit($pxValue) / stripUnit($html-font-size)}rem;
}

@mixin formStyle {
  .input-item {
    display: flex;
    align-items: center;
    margin-top: 20px;
    &.phone-number {
      select {
        width: 90px;
      }
      input[type="text"], input[type="number"] {
        width: 90px;
      }
      .ui-check {
        margin-left: 30px;
      }
    }
    &.email {
      input[type="text"], input[type="number"] {
        width: 160px;
      }
      select {
        width: 180px;
        margin-left: 12px;
      }
      .ui-check {
        margin-left: 30px;
      }
    }
    &.file {
      position: relative;
      input[type="text"] {
        width: 340px;
        background: #fff;
      }
      input[type="file"] {
        position: absolute;
        left: -9999px;
      }
      label {
        display: inline-flex;
        align-items: center;
        height: 44px;
        padding: 0 20px;
        margin-left: 20px;
        background: #333333;
        border-radius: 8px;
        font-size: 18px;
        font-weight: 500;
        color: #fff;
        cursor: pointer;
      }
      .reset-btn {
        height: 44px;
        padding: 0 20px;
        margin-left: 14px;
        border-radius: 8px;
        border: 1px solid #DDDDDD;
        font-size: 18px;
        font-weight: 500;
        color: #191919;
      }
    }

    &.select-col {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      row-gap: 16px;
      .ui-radio {
        + .ui-radio {
          margin-left: 0;
        }
      }
    }

    input[type="text"], input[type="number"] {
      width: 285px;
      &.citizen-number {
        width: 160px;
      }
      &.user-id {
        width: 320px;
      }
      &.user-pwd {
        width: 380px;
      }
    }
    .input-btn {
      height: 40px;
      margin-left: 24px;
      padding: 0 20px;
      background: #00B3BE;
      border-radius: 8px;
      font-size: 20px;
      font-weight: bold;
      color: #fff;
    }
    .ic-bar {
      position: relative;
      width: 27px;
      height: 40px;
      &::before {
        content: '';
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 5px;
        height: 1px;
        background: #000;
      }
    }
    .ic-email {
      position: relative;
      width: 27px;
      height: 40px;
      &::before {
        content: '@';
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}


html {
  font-family: "KoPubDotum", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.5;
  color: #000;
  
  input {
    font-family: "KoPubDotum", sans-serif;
    font-weight: 300;
    outline: none;
  }

  a, button {
    font-family: "KoPubDotum", sans-serif;
    font-weight: 300;
  }
}

body {
  overflow-x: hidden;
  min-width: 1280px;
}

.wrap {
  position: relative;
  max-width: 1280px;
  min-height: 768px;
  margin: 0 auto;
  &.main {
    .container {
      padding-top: 0;
    }
  }
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  height: 116px;
  &::after {
    content: '';
    position: absolute;
    left: -9999px;
    right: -9999px;
    bottom: 0;
    height: 1px;
    background: #d8d8d8;
  }
  .logo-area {
    display: flex;
    column-gap: 10px;
  }
  .link-area {
    display: flex;
    column-gap: 30px;
    margin-left: auto;
  }
  .menu-area {
    margin-left: 60px;
    .open-btn {
      width: 34px;
      height: 28px;
      background: url(../images/header_menu.png) no-repeat 0 0;
      &.on {
        display: none;
        ~ .menu-box {
          display: block;
        }
      }
    }
    .menu-box {
      display: none;
      position: absolute;
      left: 0;
      right: 0;
      top: 116px;
      min-height: 400px;
      padding: 25px 0 77px;
      z-index: 100;
      &::before {
        content: '';
        position: absolute;
        left: -9999px;
        top: 0;
        right: -9999px;
        bottom: 0;
        background: #fff;
        border-bottom: 1px solid #DDDDDD;
        z-index: 1;
      }
      > .close-btn {
        position: absolute;
        right: 3px;
        top: -74px;
        width: 27px;
        height: 27px;
        background: url(../images/ic_gnb_close.png) no-repeat 0 0;
      }
      .link-group {
        display: flex;
        position: relative;
        z-index: 10;
        &:not(.login) {
          justify-content: space-between;
          li {
            width: 200px;
          }
        }
        &.login {
          li {
            &:nth-child(1) {
              width: 180px;
            }
            &:nth-child(2) {
              width: 142px;
              margin-left: 45px;
            }
            &:nth-child(3) {
              width: 167px;
              margin-left: 3px;
            }
            &:nth-child(4) {
              width: 164px;
              margin-left: 25px;
            }
            &:nth-child(5) {
              width: 180px;
              margin-left: 24px;
            }
            &:nth-child(6) {
              width: 143px;
              margin-left: 23px;
            }
            &:nth-child(7) {
              width: 145px;
              margin-left: 11px;
            }
          }
        }
        li {
          display: flex;
          flex-direction: column;
          > .tit {
            display: block;
            font-size: 24px;
            line-height: 27px;
            font-weight: bold;
            color: #000;
          }
          .link-item {
            display: flex;
            flex-direction: column;
            margin-top: 31px;
            a {
              font-size: 19px;
              font-weight: 500;
              line-height: 48px;
              white-space: nowrap;
              color: #000;
              &.on {
                color: #00ADB2;
                text-decoration: underline;
              }
            }
          }
        }
      }
    }
  }
}

.container {
  position: relative;
  min-height: calc(100vh - 400px);
  padding-top: 80px;
  padding-bottom: 80px;
  > .sub-title {
    display: block;
    margin-top: 64px;
    font-size: 30px;
    font-weight: bold;
    color: #191919;
    + .agree-info-area {
      margin-top: 32px;
    }
  }
}

footer {
  position: relative;
  .fixed-banner-content {
    position: fixed;
    left: 50%;
    top: 160px;
    margin-left: 670px;
    z-index: 10;
    .banner-area {
      display: flex;
      flex-direction: column;
      row-gap: 24px;
      width: 100px;
      border: 1px solid #d8d8d8;
      background: #fff;
      padding: 9px 9px 22px;
      border-radius: 18px;
      a {
        display: flex;
        position: relative;
        width: 100%;
        padding-top: 87px;
        font-size: 16px;
        color: #1d1d1d;
        text-align: center;
        justify-content: center;
        letter-spacing: -1px;
        font-weight: 500;
        line-height: 23px;
        &.guide {
          &::before {
            background: #f7faff url(../images/ic_fixed_1.png) no-repeat center center;
          }
        }
        &.edu {
          &::before {
            background: #f7faff url(../images/ic_fixed_2.png) no-repeat center center;
          }
        }
        &.blog {
          &::before {
            background: #f7faff url(../images/ic_fixed_3.png) no-repeat center center;
          }
        }
        &.report {
          &::before {
            background: #f7faff url(../images/ic_fixed_4.png) no-repeat center center;
          }
        }
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 80px;
          height: 80px;
          border: 1px solid #d3dce5;
          border-radius: 18px;
        }
      }
    }
  }
  .favorite-content {
    display: flex;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      left: -9999px;
      right: -9999px;
      top: 0;
      height: 1px;
      background: #d8d8d8;
    }
    &::after {
      content: '';
      position: absolute;
      left: -9999px;
      right: -9999px;
      bottom: 0;
      height: 1px;
      background: #d8d8d8;
    }
    .link-area {
      &:first-child {
        .open-btn {
          border-left: 1px solid #d8d8d8;
        }
      }
      .open-btn {
        width: 320px;
        height: 56px;
        padding-left: 20px;
        border-right: 1px solid #d8d8d8;
        text-align: left;
        font-size: 17px;
        font-weight: 500;
        color: #000;
        background: url(../images/ic_link_plus.png) no-repeat right 28px center;
      }
    }
  }
  .info-content {
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 42px 0 41px;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: -9999px;
      right: -9999px;
      background: #f2fbfc;
      border-top: 1px solid #D8D8D8;
    }
    .ui-select {
      position: absolute;
      right: 0;
      top: 118px;
      width: 240px;
      height: 50px;
      padding: 0 20px;
      border-radius: 10px;
      border-color: #CACACA;
      background: #fff url(../images/ic_arrow_big.png) no-repeat right 20px center;
    }
    .footer-logo {
      position: relative;
      width: 192px;
      height: 51px;
      background: url(../images/footer_logo.png) no-repeat 0 0;
    }
    address {
      position: relative;
      margin-top: 23px;
      font-size: 17px;
      font-weight: 500;
      line-height: 30px;
      color: #000;
      > span {
        display: flex;
        font-size: 17px;
        line-height: 27px;
        font-weight: 500;
        color: #000000;
        &.txt {
          span + span {
            margin-left: 8px;
          }
        }
        &.tel {
          margin-top: 4px;
          span + span {
            margin-left: 7px;
          }
        }
        &.tel {
          font-weight: bold;
        }
        
      }
    }
    .link-area {
      position: relative;
      margin-top: 25px;
      border-top: 1px solid #d8d8d8;
      padding: 15px 0 0 0;
      .link-box {
        display: flex;
        column-gap: 26px;
        a {
          font-size: 17px;
          font-weight: 500;
          line-height: 27px;
          color: #000;
          &.on {
            font-weight: bold;
            color: #003675;
          }
        }
      }
      .copy {
        display: block;
        margin-top: 11px;
        font-size: 17px;
        font-weight: 500;
        line-height: 27px;
        color: #000;
      }
      .support-box {
        display: flex;
        column-gap: 22px;
        position: absolute;
        right: 0;
        bottom: 0;
      }
    }
  }
}

