      margin-bottom: 16px;
      .byte {
        font-size: 14px;
        color: #797979;
      }
      .time {
        font-size: 14px;
        color: #797979;
        i {
          font-style: normal;
          color: #FF5353;
        }
      }
    }
    textarea {
      height: 408px;
      border-radius: 20px;
    }
  }
  .table-bottom-info {
    margin-top: 16px;
    > .txt {
      display: block;
      font-size: 14px;
      color: #666666;
      &.center {
        text-align: center;
      }
    }
    > .day {
      display: block;
      margin-top: 8px;
      font-size: 14px;
      font-weight: 500;
      color: #333;
      text-align: right;
    }
    .name-check {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      column-gap: 30px;
      margin-top: 8px;
      .name {
        margin-top: 4px;
        font-size: 14px;
        font-weight: 500;
        color: #191919;
      }
      .ui-check {
        label {
          font-size: 14px;
          font-weight: 500;
          color: #191919;
          margin: 0 12px 0 0;
        }
      }
    }
  }
  .notice-info-area {
    margin-top: 24px;
    + .table-bottom-info {
      margin-top: 24px;
    }
  }
}

.com-paging {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  margin-top: 60px;
  button {
    width: 34px;
    height: 34px;
    font-size: 18px;
    font-weight: 500;
    color: #333333;
    &.on {
      background: #00b3be;
      border-radius: 6px;
      color: #fff;
    }
    &.prev {
      width: 60px;
      font-size: 18px;
      font-weight: 500;
      line-height: 36px;
      color: #333;
      padding-left: 20px;
      letter-spacing: -1px;
      background: url(../images/paging_arrow_left.png) no-repeat 0 9px;
    }
    &.next {
      width: 60px;
      font-size: 18px;
      font-weight: 500;
      line-height: 36px;
      color: #333;
      padding: 0 20px 0 0;
      letter-spacing: -1px;
      background: url(../images/paging_arrow_right.png) no-repeat right 9px;
    }
  }
  .empty {
    width: 34px;
    height: 34px;
    font-size: 18px;
    font-weight: 500;
    color: #333333;
    text-align: center;
  }
}



/* 레이어 팝업 */
.layer-pop {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  &.on {
    display: block;
  }
  &.simple-type {
    .layer-content {
      width: 606px;
      .layer-scroll {
        padding: 60px 118px;
        background: #fff;
      }
    }
  }
  .layer-content {
    overflow: hidden;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 840px;
    border-radius: 20px;
    .layer-scroll {
      overflow-y: auto;
      position: relative;
      max-height: calc(100vh - 230px);
      padding: 56px 56px 49px;
      background: #F8F8F8;
      > .title {
        display: block;
        position: relative;
        font-size: 26px;
        font-weight: bold;
        text-align: center;
        color: #191919;
        .ic-edu {
          position: absolute;
          left: 0;
          top: 0;
          width: 176px;
          height: 34px;
          background: url(../images/ic_pop_edu.png) no-repeat 0 0;
        }
      }
      .close-btn {
        position: absolute;
        right: 38px;
        top: 15px;
        width: 32px;
        height: 32px;
        background: url(../images/ic_pop_close.png) no-repeat center center;
      }
      .send-input-area {
        margin-top: 32px;
        .send-box {
          > .tit {
            display: block;
            font-size: 16px;
            font-weight: bold;
            color: #333;
          }
          + .send-box {
            margin-top: 40px;
          }
          .input-group {
            display: flex;
            margin-top: 12px;
            input[type="text"] {
              flex: 1;
              height: 38px;
              border-radius: 6px;
            }
            .send-btn {
              height: 38px;
              margin-left: 12px;
              padding: 0 20px;
              border-radius: 6px;
              border: 1px solid #DDD;
              font-size: 16px;
              font-weight: bold;
              color: #333;
            }
          }
        }
        .notice-txt {
          display: block;
          margin-top: 16px;
          font-size: 14px;
          color: #555;
          padding-left: 15px;
          text-indent: -15px;
        }
      }
      .info-area {
        margin-top: 32px;
        border-radius: 20px;
        border: 1px solid #DDDDDD;
        background: #fff;
        padding: 30px;
        > .tit {
          display: block;
          margin-bottom: 14px;
          font-size: 20px;
          font-weight: bold;
          color: #333;
        }
        /*
        .table-box {
          > .txt {
            display: block;
            font-size: 14px;
            color: #555555;
          }
          table {
            margin-top: 24px;
            border-top: 1px solid #DDDDDD;
            th {
              padding: 10px 20px;
              background: #F5F5F5;
              border-right: 1px solid #DDDDDD;
              border-bottom: 1px solid #DDDDDD;
              text-align: center;
              font-size: 14px;
              color: #191919;
              &:last-child {
                border-right: 0;
              }
            }
            td {
              padding: 10px 20px;
              border-right: 1px solid #DDDDDD;
              border-bottom: 1px solid #DDDDDD;
              font-size: 13px;
              color: #555;
              &:last-child {
                border-right: 0;
              }
            }
          }
        }
        */
        .list-box {
          width: 267px;
          margin: 0 auto;
          li {
            display: flex;
            align-items: center;
            + li {
              margin-top: 24px;
            }
            .tit {
              flex-shrink: 0;
              width: 100px;
              font-size: 16px;
              font-weight: bold;
              color: #333333;
            }
            input[type="text"] {
              font-size: 14px;
            }
          }
        }
        .result-txt {
          margin-top: 56px;
          background: #F6FEFF;
          border-radius: 12px;
          border: 1px solid #ddd;
          padding: 20px;
          text-align: center;
          font-size: 16px;
          font-weight: 500;
          color: #191919;
        }
        .confirm-box {
          margin-top: 28px;
          > .text {
            display: block;
            font-size: 14px;
            text-align: center;
            color: #555555;
          }
          .check-group {
            display: flex;
            justify-content: center;
            margin-top: 27px;
          }
        }
        .text-box {
          padding-bottom: 22px;
          > .tit {
            display: block;
            font-size: 16px;
            color: #555;
            + .txt {
              padding: 8px 0 0 12px;
            }
          }
          > .txt {
            display: block;
            font-size: 14px;
            color: #797979;
            + .tit {
              margin-top: 26px;
            }
          }
          > .noti-txt {
            display: block;
            margin-top: 90px;
            font-size: 14px;
            font-weight: bold;
            text-align: center;
            color: #191919;
          }
          > .day {
            display: block;
            margin-top: 12px;
            font-size: 14px;
            font-weight: bold;
            color: #191919;
            text-align: right;
          }
          .textarea-group {
            margin-top: 32px;
            > .tit {
              display: block;
              font-size: 20px;
              font-weight: bold;
              color: #333333;
            }
            .txt {
              display: block;
              margin-top: 6px;
              font-size: 14px;
              color: #555555;
            }
            .check-item {
              margin-top: 24px;
              padding: 21px 24px;
              border-radius: 12px;
              border: 1px solid #DDDDDD;
              .check-cnt {
                + .check-cnt {
                  margin-top: 12px;
                }
                label {
                  font-size: 14px;
                }
              }
            }
            .text-item {
              margin-top: 50px;
              .txt {
                display: block;
                font-size: 14px;
                color: #555555;
              }
              textarea {
                margin-top: 8px;
              }
            }
          }
          .table-group {
            margin-top: 30px;
            > .tit {
              display: block;
              font-size: 20px;
              font-weight: bold;
              color: #333;
            }
            + .txt {
              margin-top: 10px;
            }
            + .table-group {
              margin-top: 24px;
              table {
                border-top: 1px solid #DDDDDD;
              }
            }
            table {
              width: 100%;
              margin-top: 12px;
              border-top: 1px solid #191919;
              th {
                height: 40px;
                background: #EEEEEE;
                border-right: 1px solid #DDDDDD;
                border-bottom: 1px solid #DDDDDD;
                text-align: center;
                font-size: 14px;
                font-weight: bold;
                color: #555;
                &:last-child {
                  border-right: 0;
                }
              }
              td {
                padding: 10px;
                border-right: 1px solid #DDDDDD;
                border-bottom: 1px solid #DDDDDD;
                font-size: 14px;
                color: #797979;
                &:last-child {
                  border-right: 0;
                }
                &.center {
                  text-align: center;
                  color:#191919;
                }
              }
            }
          }
        }
      }
      .btn-box {
        display: flex;
        justify-content: center;
        padding-top: 52px;
        //border-top: 1px solid #E8E8E8;
        button {
          min-width: 74px;
          height: 38px;
          padding: 0 15px;
          border-radius: 6px;
          background: #00B3BE;
          font-size: 16px;
          font-weight: bold;
          color: #FFFFFF;
        }
      }
    }
    
  }
}

